.container{
  background-color: #a36dad80;
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 500px;
  margin: 0 auto;
  border-radius: 20px;
  margin-top: 50px;
}
form{
  display: flex;
  flex-direction: column;
}
form input{
  padding: 10px;
  margin: 10px;
  border: none;
  outline: none;
  border-radius: 10px;
}
form button{
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  border: none;
  outline: none;
  color: white;
  background-color: rgb(96, 209, 130);
  font-weight: 600;
  font-size: 20px;
}
form p{
  color: red;
}


.light {
  background-color: #f2f2f2;
  color: #333;
}

.dark {
  background-color: #333;
  color: #f2f2f2;
}

.theme-button{
  /* display: flex; */
  border: none;
  outline: none;
  width: fit-content;
  justify-content: flex-end;
  padding: 20px;
  background-color: transparent;
  /* right: o.5px !important; */


}
.theme-icon {
  /* Common icon styles */
  font-size: 25px;
}

.dark-icon {
  color: #f2f2f2;
}

.light-icon {
  color: #333;
}

